@import url('https://fonts.googleapis.com/css2?family=Asap:wght@100&display=swap');

.startContainer {
  display:flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  width: 100%;

}


.top-right-puddle-start{
  position: absolute;
  top: 0px;
  left : 120px;
  transform: rotate(90deg);
  width: 100%;
  opacity: 0;
  animation: both 1s cubic-bezier(0,.02,.75,1.1) 0s;
}

.top-right-puddle-start.top-right-puddle-start-animate{
  animation-name: top-right-puddle-start-animation;
}

@keyframes top-right-puddle-start-animation {
  from {
    transform: rotate(90deg) translate(-40px, -30px);
    opacity: 0;
  }
  to {
    transform: rotate(90deg) translate(0px, 0px);
    opacity: 0.7;
    
  }
}


.bottom-puddle-start{
  position: absolute;
  bottom: -100px;
  right: 120px;
  transform: rotate(90deg);
  width: 100%;
  max-width: 100%;
  opacity: 0;
  animation: both 1s cubic-bezier(0,.02,.75,1.1) 0s;
}

.bottom-puddle-start.bottom-puddle-start-animate{
  animation-name: bottom-puddle-start-animation;
}

@keyframes bottom-puddle-start-animation {
  from {
    transform: rotate(90deg) translate(40px, 30px);
    opacity: 0;
  }
  to {
    transform: rotate(90deg) translate(0px, 0px);
    opacity: 0.7;
    
  }
}

.top-left-puddle-start{
  position: absolute;
  top: 90px;
  right: 320px;
  transform: rotate(120deg);
  width: 100%;
  opacity: 0;
  animation: both 1s cubic-bezier(0,.02,.75,1.1) 0s;
}

.top-left-puddle-start.top-left-puddle-start-animate{
  animation-name: top-left-puddle-start-animation;
}

@keyframes top-left-puddle-start-animation {
  from {
    transform: rotate(120deg) translate(-0px, 30px);
    opacity: 0;
  }
  to {
    transform: rotate(120deg) translate(0px, 0px);
    opacity: 0.7;
  }
}

.start-background-image-container {
  display:flex;

  align-items: center;
  justify-content: center;
}

.startBackgroundImage{
  object-fit: cover;

}

.startButtonContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  position: absolute;
  bottom: -100px;
}

.begin-button {
  
  font-family: 'Asap', sans-serif;
  margin: 10px;
  border: none;
  border-radius: 20px;
  justify-content: center;
  text-align: center;
  width: 260px;
  height: 45px;
  background: linear-gradient(22deg, #1b4744 20%,  #52d9d1 39%, #1f7f7a 50%);
  color: #ffffff;
  font-size: 16px;
  font-weight: 1000;
  text-shadow: -1px 5px 5px rgb(0, 0, 0),  0 0 2px #2DB8B0, 0 0 2px #2DB8B0, 0 0 2px #2DB8B0, 0 0 2px #2DB8B0, 0 0 2px #2DB8B0, 0 0 2px #2DB8B0, 0 0 2px #2DB8B0, 0 0 2px #2DB8B0, 0 0 2px #2DB8B0, 0 0 2px #2DB8B0, 0 0 2px #2DB8B0, 0 0 2px #2DB8B0, 0 0 2px #2DB8B0, 0 0 2px #2DB8B0, 0 0 2px #2DB8B0, 0 0 2px #2DB8B0;
  animation:  both 1s cubic-bezier(0,.4,.74,1.21) 0.6s;
  opacity: 0;
}

.begin-button-animate{
 animation-name: begin-button-animation;
}

@keyframes begin-button-animation  {
  0% {
      z-index: 0;
      transform: scale(0);
      color: #ffffff00;
      opacity: 0;

  }
  50% {
    z-index: 1;
    transform: scale(1);
    opacity: 1;
    
    
  }

  85% {
    padding-bottom: 2px;
    color: #ffffff00;
  }
  88% {
    padding-bottom: 0px;
    padding-top: 2px;
  }

  92% {
    padding-bottom: 2px;
    padding-top: 0px;
    
  }

  100%{
    opacity: 1;
    padding: 0px;
    color: #ffffff;
    box-shadow: 0px 0px 11px 10px white, inset 5px 0px 4px 0px #1f7f7a;
  }
}

.glassContainer{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;
    position: absolute;
}

.glass-img{
    opacity: 0;
    width: 100%;
    object-fit: cover;
    position: relative;
    animation: both 1.5s cubic-bezier(0,.4,.74,1.21) 1s;
    transition: 1.5s cubic-bezier(0,.4,.74,1.21) 1s;
}

.glass-img-animate{
  animation-name: glass-animation ;
   
}

@keyframes glass-animation {
  from {
    
    transform: translate(00px, -50px) rotate(0deg);
    opacity: 0;
  }
  to {
    transform: translate(0px) rotate(0deg);
    opacity: 1;
    
  }
}

.fadeOut {
  animation: fadeOutAnimation 1s forwards;
}

@keyframes fadeOutAnimation {
  to {
    opacity: 0;
    visibility: hidden;
  }
}

