@import url('https://fonts.googleapis.com/css2?family=Questrial&display=swap');
html{
  overflow: hidden;
}

body {
  overflow: hidden;
  margin: 0;
  padding: 0px;
  background: rgb(255,208,172);
  background: radial-gradient(circle, rgba(255,208,172,1) 0%, rgba(253,114,53,1) 100%);
  position: relative;
}


html, body, #root, .parent {
  height: 100%;
  margin: 0;
  padding: 0;
}