.signupContainer{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 100vh;
    width: 100%;
    position: relative;
}

.top-right-puddle-signup{
  position: absolute;
  top: -100px;
  left : 200px;
  transform: rotate(40deg);
  width: 100%;
  opacity: 1;
  animation: both 1s cubic-bezier(0,.02,.75,1.1) 0s;
}

.top-left-puddle-signup{
  position: absolute;
  top: -100px;
  right : 240px;
  transform: rotate(120deg);
  width: 100%;
  opacity: 1;
  animation: both 1s cubic-bezier(0,.02,.75,1.1) 0s;
}

.bottom-left-puddle-signup{
  position: absolute;
  top: 70%;
  right : 220px;
  transform: rotate(80deg);
  width: 100%;
  opacity: 1;
  animation: both 1s cubic-bezier(0,.02,.75,1.1) 0s;
}

.signupTitle{
  color: #ffffff;
  margin-bottom: 60px;
}

.signup-content-container {
  display:flex;
  width: auto;
  height:100%;
  align-items: center;
  justify-content: center;
}

.signupBackgroundImage {
    object-fit: cover;
    height: 100%;
    width: 100%;
  }


  .signupTextContainer{
    position:absolute;
    padding:50px;
    display:flex;
    flex-direction: column;
  }


  .signupInputContainer{
    display:flex;
    flex-direction: column;
    margin-bottom: 15px;
  }

  .signupInputContainer input{
    background-color: transparent;
    border-style: none;
    border-bottom: 2px solid white;
    outline: none;
  }

  .signupInputContainer label{
    color:#ffffff;
    font-weight: bold;
    font-size:18px;
    margin-bottom: 10px;
  }

  .signupRgpdContainer{
    display:flex;
    flex-direction: row;
    align-items: center;
  }

  .signupRgpdText{
    font-size:12px;
    text-align: justify;
    color:#ffffff;
    margin-left: 10px;
  }

  .signupRgpdCheckbox {
    width: 100px; 
    height: 100px; 
    border:none;
  }
  
  .signupButtonContainer{
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
  }

  .signupButtonContainer button {
    border: 0px solid black;
    color: #2DB8B0;
    font-size: 18px;
    font-weight: 800;
    padding: 20px;
    font-family: 'Questrial', sans-serif;
    background: linear-gradient(0deg, rgba(255,255,255,1) 10%, rgba(214,214,214,1) 95%);
    text-shadow: 0px 4px 10px rgb(86, 86, 86);
    width: 260px;
    border-radius: 30px;
  }

  .signupButtonContainer button::after {
    content: '';
    position: absolute;
    border-radius: 100%;
    z-index: -1;
    margin-top: 30px;
    left: 50%;
    width: 20%;
    height: 10px;
    transform: translateX(-50%);
    box-shadow: 0px 2px 10px rgb(0, 0, 0);
}

  .signupSuccess{
    font-weight: bold;
  }


.linkToSignin{
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: underline;
  color: #ffffff;
}


.linkToSignin p{
  font-weight: bold;
  color: #ffffff;
  text-align: center;
}


.footer-link{
  position:absolute;
  display:flex;
  flex-direction: column;
  bottom: 0;
}
.p-back-login{
  color: w;
}


.footer-link p {
  color: #40B4A9;
  text-decoration: none;
 font-weight: bold;
 position: relative;
}

.signupError{
  color: black;
  font-size: 12px;
  margin-top: 5px;
  text-align: center;
}

.fadeIn {
  animation: fadeInAnimation 1s;
}

.fadeOut {
  animation: fadeOutAnimation 1s;
}

@keyframes fadeInAnimation {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes fadeOutAnimation {
  from { opacity: 1; }
  to { opacity: 0; }
}