.signContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100vh;
  width: auto;
  position: relative;
}



.sign-content-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  opacity: 0;
  animation: both 1s cubic-bezier(0,.02,.75,1.1) 0s;
}



.sign-content-container.signin-animate {
    animation-name: signin-first-animation; 
  }

@keyframes signin-first-animation{
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
      
    }
  }

  .puddle-singin-right{
    position: absolute;
    width: 100%;
    transform: rotate(90deg);
    bottom: 18%;
    left: 50%;
    opacity: 0.7;
}

.puddle-singin-bottom-left{
  position: absolute;
    width: 100%;
    transform: rotate(90deg);
    top: 78%;
    right: 50%;
    opacity: 0.7;
}

.puddle-singin-top-left{
  position: absolute;
    width: 100%;
    transform: rotate(120deg);
    bottom: 66%;
    right: 70%;
    opacity: 0.7;
}

.sinLogoContainer {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 80vh;
}

.logo-sign {
  width: 180px;
  top : 20px;
  position: absolute;
}

.sinTextContainer {
  position: absolute;
  padding: 40px;
  display: flex;
  flex-direction: column;
  margin-top: 120px;
  margin-bottom: 10px;

}

.sinTitle {
  margin-bottom: 40px;
  margin-top: 40px;
}

.sinWelcomeTitle {
  font-size: 35px;
  text-align: center;
  color: #ffffff;
  font-weight: 300;
}

.sinWebsiteLink {
  text-align: center;
  color: #40b4a9;
  letter-spacing: 2px;
  font-size: 12px;
  font-weight: bold;
}

.sinInputContainer {
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
}

.sinInputContainer input {
  background-color: transparent;
  border-style: none;
  border-bottom: 2px solid white;
  outline: none;
}

.sinInputContainer label {
  color: #ffffff;
  font-weight: bold;
  font-size: 15px;
  margin-bottom: 10px;
}

.sinRgpdText {
  font-size: 8px;
  text-align: justify;
  color: #ffffff;
  margin-left: 10px;
}

.sinRgpdCheckbox {
  width: 100px;
  height: 100px;
  outline: none;
  border: none;
  padding: 0;
}

.sinButtonContainer {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
}

.sinButtonContainer button {
    border: 0px solid black;
    color: #2DB8B0;
    font-size: 18px;
    font-weight: 800;
    padding: 20px;
    font-family: 'Questrial', sans-serif;
    background: linear-gradient(0deg, rgba(255,255,255,1) 10%, rgba(214,214,214,1) 95%);
    text-shadow: 0px 4px 10px rgb(86, 86, 86);
    
    width: 260px;
    border-radius: 30px;
}

.sinButtonContainer button::after {
    content: '';
    position: absolute;
    border-radius: 100%;
    z-index: -1;
    margin-top: 30px;
    left: 50%;
    width: 20%;
    height: 10px;
    transform: translateX(-50%);
    box-shadow: 0px 2px 10px rgb(0, 0, 0);
}

.forgotPassword {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: underline;
  color: #ffffff;
}

.forgotPassword p {
  font-weight: bold;
  color: #ffffff;
}

.linkToSignup {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: underline;
  color: #ffffff;
}

.linkToSignup p {
  font-weight: bold;
  color: #ffffff;
}

.sinInputContainer {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}

.sinInputContainer input {
  background-color: transparent;
  border-style: none;
  border-bottom: 2px solid white;
}

.sinInputContainer label {
  color: #ffffff;
  font-weight: bold;
  font-size: 18px;
  margin-bottom: 10px;
}

.sinRgpdContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.sinRgpdText {
  font-size: 12px;
  text-align: justify;
  color: #ffffff;
  margin-left: 10px;
}

.sinRgpdCheckbox {
  width: 100px;
  height: 100px;
  border: none;
}

.sinButtonContainer {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
}


.sinError {
  color: black;
  font-size: 12px;
  margin-top: 5px;
  text-align: center;
}

.fadeOut {
  animation: fadeOutAnimation 1s forwards;
}

@keyframes fadeOutAnimation {
  from {
    opacity: 1;
}
  to {
    opacity: 0;
    visibility: hidden;
  }
}
/* Petit smartphone */
@media screen and (max-width: 500px) and (max-height: 780px) {
 


.logo-sign{
  position: absolute;
  width: 100px;
  
  top: 2px;
}
  .sign-content-container {
    margin-bottom: 120px;
  }

  .sinWelcomeTitle{
    position: relative;
    top: 50px;
  }

  .sinWebsiteLink{
    position: relative;
    top: 35px;
  }


  



}
