.welcome-container{
  display:flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  width: 100vw;
  font-family: 'Questrial', sans-serif;
  opacity: 0;
  animation: both 1s cubic-bezier(0,.02,.75,1.1) 0s;
}

.welcome-container.welcome-animate {
    animation-name: welcome-first-animation; 
  }

@keyframes welcome-first-animation{
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
      
    }
  }

.top-left-puddle-welcome{
    position: absolute;
    width: 100%;
    transform: rotate(120deg);
    bottom: 65%;
    right: 60%;
    opacity: 0.7;
}

.bottom-left-puddle-welcome{
    position: absolute;
    width: 100%;
    transform: rotate(90deg);
    top: 78%;
    right: 50%;
    opacity: 0.7;
}

.bottom-right-puddle-welcome{
    position: absolute;
    width: 100%;
    transform:  rotate(120deg);
    top: 62%;
    left: 75%;
}

.welcome-in{
    position: relative;
    bottom: 200px;
    font-size: 35px;
    color: white;
}

.title-welcome{
    display: flex;
    position: relative;
    bottom: 220px;
    color: white;
    gap: 20px;
    text-align: center;
}

.title-welcome img {
    width: 130px;
}

.savor-text-one{
    text-align: center;
    position: relative;
    bottom: 210px;
    color: #1F7F7A;
    font-size: 1.4em;
    font-family: 'Questrial', sans-serif;
}

.logo-welcome{
    bottom: 60px;
    position: relative;
    width: 250px;
    filter: drop-shadow(30px 10px 15px rgb(192, 186, 186));
    animation: both 1s cubic-bezier(0,.02,.75,1.1) 1s;
    transform: scale(0.8);
}

.logo-welcome.logo-welcome-animate{
    animation-name: welcome-logo-animation;
}
@keyframes welcome-logo-animation{
    from {
        transform: scale(0.8);
        filter: drop-shadow(0px 10px 15px black);
    }
    to {
      transform: scale(1);
      filter: drop-shadow(30px 10px 15px rgb(109, 99, 99));
      
    }
  }

.tutoriel-button{
    border: 0px solid black;
    color: #e1e6e6;
    font-size: 18px;
    font-weight: 100;
    padding: 20px;
    font-family: 'Questrial', sans-serif;
    background: linear-gradient(0deg, rgba(255,255,255,1) 10%, rgba(214,214,214,1) 95%);
    text-shadow: 0px 5px 8px rgb(0, 0, 0),  0 0 2px #2DB8B0,  0 0 1px #2DB8B0, 0 0 2px #2DB8B0, 0 0 2px #2DB8B0, 0 0 2px #2DB8B0, 0 0 2px #2DB8B0, 0 0 2px #2DB8B0, 0 0 2px #2DB8B0, 0 0 2px #2DB8B0, 0 0 2px #2DB8B0, 0 0 2px #2DB8B0, 0 0 2px #2DB8B0;
    width: 260px;
    
    border-radius: 30px;
    position: relative;
    bottom: 190px;
}

.savor-text-two{
    padding: 10px;
    position: relative;
    text-align: center;
    color: #1F7F7A;
    font-size: 1.2em;
    font-family: 'Questrial', sans-serif;
    bottom: 180px;

}

.ignore-button{
    color: #1F7F7A;
    text-decoration: underline;
    font-family: 'Questrial', sans-serif;
    background: none;
    font-weight: 700;
    border: 0px solid black;
    position: absolute;
    bottom: 50px;
    right: 80px;
}

/* Petit smartphone */
@media screen and (max-width: 500px) and (max-height: 780px) {
    .welcome-in{
        position: relative;
        bottom: 200px;
        font-size: 35px;
        color: white;
    }
    
    .title-welcome{
        display: flex;
        position: relative;
        bottom: 220px;
        color: white;
        gap: 20px;
        text-align: center;
    }
    
    .title-welcome img {
        width: 130px;
    }
    
    .savor-text-one{
        text-align: center;
        position: relative;
        bottom: 210px;
        color: #1F7F7A;
        font-size: 1.4em;
        font-family: 'Questrial', sans-serif;
    }
    
    .logo-welcome{
        bottom: 60px;
        position: relative;
        width: 190px;
        filter: drop-shadow(30px 10px 15px black);
        animation: welcome-logo-animation both 1s cubic-bezier(0,.02,.75,1.1) 1s;
        
    }
}



